.footer {
    background-color: @darkBlue;
    padding: 41px 0;

    &__wrap {
        display: flex;
    }

    .logo {
        max-width: 131px;
    }

    &__content {
        padding-left: 170px;
        padding-top: 10px;
    }

    &__menu {
        display: flex;
        color: @white;
        font-family: @opensans;
        font-size: 13px;
        text-transform: uppercase;
        padding: 0;
        margin: 0 0 20px;
        list-style: none;

        &-item {
            &:not(:last-of-type) {
                margin-right: 50px;
            }
        }
    }

    &__contacts {
        display: flex;

        &-item {
            position: relative;
            color: @white;
            font-family: @opensans;
            font-size: 14px;
            margin: 0;
            line-height: 1;

            &:not(:last-child) {
                margin-right: 40px;
                &:after {
                    content: '';
                    position: absolute;
                    right: -21px;
                    top: -4px;
                    width: 1px;
                    height: 160%;
                    background-color: @white;
                    opacity: 0.3;
                }
            }
        }
    }

    .rights {
        border-top: 1px solid rgba(255,255,255,0.3);
        margin-top: 28px;
        justify-content: space-between;
        padding-top: 20px;

        &__item {
            color: @white;
            font-size: 12px;
            font-family: @opensans;

            a {
                &:not(:last-of-type) {
                    margin-right: 28px;
                }
            }

            &.siluet {

                a {
                    position: relative;

                    &:before {
                        content: '';
                        position: absolute;
                        top: -4px;
                        left: -23px;
                        width: 20px;
                        height: 22px;
                        background-image: url('../img/icons/logo_siluet.png');
                        background-size: cover;
                        background-repeat: no-repeat;
                        background-position: center;
                    }
                }
            }
        }
    }
}