.about__block ol li:not(:last-of-type) {
  margin-bottom: 16px;
}
.about__block ol {
  counter-reset: number;
}
.about__block ol li:before {
  left: 0;
  top: 0;
  position: absolute;
  counter-increment: number;
  content: counter(number) ".";
}
